<template>
	<div>
		<Loader type="sentences" v-if="isLoading" />
		<div v-else>
			<v-card class="mb-6">
				<v-card-title>
					{{ detailTraction.judul }}
				</v-card-title>
				<!-- <v-card-text>
					Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
					tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam.
				</v-card-text> -->
			</v-card>
			<v-alert 
				v-if="detailTraction.detail.length === 0"
				type="info">
				Belum ada jawaban
			</v-alert>
			<v-card 
				v-else
				class="mb-6" 
				v-for="(item, i) in detailTraction.detail" :key="i">
				<v-card-title class="pt-3 pb-0">
					{{ item.pertanyaan }}
				</v-card-title>
				<v-card-text>
					<v-text-field
						:value="item.jawaban"
						readonly
						dense
					></v-text-field>
				</v-card-text>
			</v-card>
		</div>  
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'

	export default{
		props: ['traction_id', 'startup_id'],
		components: { Loader },
		created() {
			this.$store.dispatch('dataIncubatorTraction/getDataTractionResponse', {
				traction_id: this.traction_id,
				startup_id: this.startup_id
			})
      this.$store.dispatch('dataIncubatorTraction/updateIsLoading', true)	
		},
		computed: {
			...mapState('dataIncubatorTraction', ['detailTraction', 'isLoading'])
		}
	}
</script>